import { Component, OnInit, OnDestroy } from '@angular/core';

//import activated route related code
import { ActivatedRoute, Router} from "@angular/router";
import { BlogService } from '../blog.service';
import { BlogHttpService } from '../blog-http.service';
import { ToastrService } from 'ngx-toastr';
import { Location } from "@angular/common";
 
@Component({
  selector: 'app-blog-view',
  templateUrl: './blog-view.component.html',
  styleUrls: ['./blog-view.component.css'],
  providers:[Location]
})
export class BlogViewComponent implements OnInit,OnDestroy{
  public currentBlog;
  //public con;

    constructor(private location : Location,private _route:ActivatedRoute , private router : Router, public toastr: ToastrService,public blogserve : BlogService,public bloghttpservice:BlogHttpService) { 
    console.log("constructor is called");
  }

  ngOnInit() {
    //this.con = new BlogService();
    console.log("blog-view ngOnInitCalled");
    //getting the blog id from the route
    let myBlogId = this._route.snapshot.paramMap.get('blogId');
    console.log("My Blog Id: ",myBlogId);
    
    // this.currentBlog=this.blogserve.getSingleBlogInformation(myBlogId);

    this.currentBlog=this.bloghttpservice.getSingleBlogInformation(myBlogId).subscribe(
      data=> {
        console.log(data);
        this.currentBlog=data["data"];
      },
      error =>{
        console.log("some error occured");
        console.log(error.errorMessage);
      }


    );
    console.log(this.currentBlog);
  }

  public deleteThisBlog():any{
    this.bloghttpservice.deleteBlog(this.currentBlog.blogId).subscribe(
      data=>{
        console.log("blog deleted");
        console.log(data);
        this.toastr.success("Blog successfully deleted","Success");
        setTimeout(()=>{
              this.router.navigate(['/home']);
        },1000)
      },
      error=>{
          console.log("error occuredd in deleting the blog")
          console.log(error.message);
          this.toastr.error("blog is not deleted")
      }
    )
  }

  public goBackToPreviousPage(){
    this.location.back();
  }

  ngOnDestroy() {
    console.log("blog-view ng On It Destroyed");

  }




}
