import { Component, OnInit,ViewContainerRef } from '@angular/core';
import { BlogHttpService } from '../blog-http.service';
import { ActivatedRoute, Router} from '@angular/router';
//  import { ToastsManager } from 'ng2-toastr/ng2-toastr';
 import { ToastrService } from 'ngx-toastr';



@Component({
  selector: 'app-blog-create',
  templateUrl: './blog-create.component.html',
  styleUrls: ['./blog-create.component.css']
  //template: '<button class="btn btn-default" (click)="showSuccess()">Toastr Tester</button>'
})
export class BlogCreateComponent implements OnInit {

  public blogTitle:string;
  public blogDescription :string;
  public blogBodyHtml :string;
  public blogCategory : string;
  public possibleCategories = ["humour","comedy","Technical"];



  constructor(public blogHttpservice : BlogHttpService,private _route:ActivatedRoute,private router:Router,private toastr: ToastrService) {
    
  }



  ngOnInit() {
  }



  public createBlog():any{

    let blogData = {
      title : this.blogTitle,
      description: this.blogDescription,
      category:this.blogCategory,
      blogBody:this.blogBodyHtml

    }

    console.log(blogData);

    this.blogHttpservice.createBlog(blogData).subscribe(

      data=>{
        console.log("blog created sucessufully");
        console.log(data);
        
        setTimeout(()=>{
          this.router.navigate(['/blog',data.data.blogId]);
        },1000)

       
          // this.toastr.success("blog Posted sucessufully","success");
          this.toastr.success('blog Posted sucessufully"', 'Toastr fun!');
      },

      error=>{
        console.log("error found in blog creation");
        console.log(error.errorMessage);
        // this.toastr.error("some error occured", 'Oops!');
        this.toastr.error('error found in blog creation', 'Major Error', {
          timeOut: 3000
        });
      }

    )

  }

}
