import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute, Router } from '@angular/router';
import { BlogHttpService } from '../blog-http.service';

@Component({
  selector: 'app-blogedit',
  templateUrl: './blogedit.component.html',
  styleUrls: ['./blogedit.component.css']
})
export class BlogeditComponent implements OnInit {
  public currentBlog;
  public possibleCategories = ["humour", "comedy", "Technical"];

  constructor(public blogHttpservice: BlogHttpService, private _route: ActivatedRoute, private router: Router, private toastr: ToastrService) {

  }

  ngOnInit() {

    console.log("blog-edit ngOnInitCalled");
    //getting the blog id from the route
    let myBlogId = this._route.snapshot.paramMap.get('blogId');
    console.log("My Blog Id: ", myBlogId);

    // this.currentBlog=this.blogserve.getSingleBlogInformation(myBlogId);

    this.currentBlog = this.blogHttpservice.getSingleBlogInformation(myBlogId).subscribe(
      data => {
        console.log(data);
        this.currentBlog = data["data"];
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage);
      }


    );
    console.log(this.currentBlog);

  }

  public editThisBlog() {

    this.blogHttpservice.editBlog(this.currentBlog.blogId, this.currentBlog).subscribe(
      data => {
        console.log("blog edited sucessfully");
        this.toastr.success("blog is Edited Sucessufully", "Success");
        setTimeout(() => {
          this.router.navigate(['/blog',this.currentBlog.blogId]);
        }, 1000)
      },
      error=>{
        console.log("error occured in editing blog");
        this.toastr.error("error occured in editing blog")
      }
    )

  }

}
